import { Component, OnInit, OnDestroy, AfterViewInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { App } from '../../+builder/models/model';
import { Script } from '../../../shared/services/index';
import { AnalyticService } from '../services/analytic.service';

declare var jQuery: any;
declare var window: any;
declare var document: any;

@Component({
  selector: 'Temp',
  template: `
        <div [ngSwitch]="Temp_name">
            <one-page-slider *ngSwitchCase="'one-page-slider'"
            [JSON_Template]="JSON_Template"
            class="main-body {{jsonBuilderHelper.getJSONBuilt().themeColor}}"
            [devMode]="devMode"
            >
            </one-page-slider>
            <sound-cloud *ngSwitchCase="'sound-cloud'"
            [JSON_Template]="JSON_Template"
            [devMode]="devMode"
            class="{{jsonBuilderHelper.getJSONBuilt().themeColor}}"
            >
            </sound-cloud>
            <one-page-card *ngSwitchCase="'one-page-card'"
            [JSON_Template]="JSON_Template"
            [devMode]="devMode"
            class="{{jsonBuilderHelper.getJSONBuilt().themeColor}}"
            >
            </one-page-card>
        </div>

      `,
})

export class Template implements OnInit, AfterViewInit, OnDestroy {
  Temp_name: String;
  @Input() private JSON_Template: any;
  private sub: any;
  oldRealTime: boolean;
  devMode: boolean = false;

  constructor(private route: ActivatedRoute,
    private router: Router,
    private jsonBuilderHelper: JSONBuilder,
    private _analyticService: AnalyticService,
    private _script: Script
  ) {

  }

  ngOnInit() {
    window.Intercom('update', { hide_default_launcher: true });
    if (!this.JSON_Template) {
      let template = localStorage.getItem('template');
      if (template) {
        this.oldRealTime = JSON.parse(template).realTime;
        this.JSON_Template = new App().deserialize(JSON.parse(template));
        this.JSON_Template.realTime = this.oldRealTime;
      }
    }
    else {
      this.oldRealTime = this.JSON_Template.realTime;
      this.JSON_Template = new App().deserialize(this.JSON_Template);
      this.JSON_Template.realTime = this.oldRealTime;
    }
    this.jsonBuilderHelper.setTemplate(this.JSON_Template);

    this._script.load('rangeSlider', 'math','iFrameResizer')
      .then((data) => {
        console.log('Scripts Loaded', data);
        this.Temp_name = this.JSON_Template.template;
      })
      .catch((error) => {
        //any error
      });

    if (this.JSON_Template.status == 'LIVE') {
      //UTM Code Start
      let queryURL: string = ''; let utm_param: any = {};
      let url: string = window.location.href;
      if (document.referrer == '' || document.referrer == undefined) {
        queryURL = url;
      } else {
        queryURL = document.referrer;
      }
      if (queryURL.length) {
        //null means query param is absent, "" (present with no value), else it will return a value
        var utm_source = this.getParameterByName('utm_source', queryURL);
        var utm_medium = this.getParameterByName('utm_medium', queryURL);
        var utm_campaign = this.getParameterByName('utm_campaign', queryURL);
        var utm_term = this.getParameterByName('utm_term', queryURL);
        var utm_content = this.getParameterByName('utm_content', queryURL);
        utm_param = {
          utm_source: ((utm_source) ? utm_source : ''),
          utm_medium: ((utm_medium) ? utm_medium : ''),
          utm_campaign: ((utm_campaign) ? utm_campaign : ''),
          utm_term: ((utm_term) ? utm_term : ''),
          utm_content: ((utm_content) ? utm_content : '')
        }
      }
      //UTM Code End

      this._analyticService.generateVisitorKey(this.jsonBuilderHelper.getJSONBuilt()._id, this.jsonBuilderHelper.getTemplateQuestionare(), utm_param)
        .subscribe(
        (response: any) => {
          this._analyticService.setVisitorKey(response.key);
        },
        (error: any) => {
          console.log(error);
        }
        );
    }
  }

  getParameterByName(name: any, url: any) {
    name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
      results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  }

  ngAfterViewInit() {
    jQuery('meta[name=description]').attr('content', this.JSON_Template.description);
    document.title = this.JSON_Template.title;
    jQuery('#favicon').attr('href', this.JSON_Template.favicon);
  }

  ngOnDestroy() {
    window.Intercom('update', { hide_default_launcher: false });
    // this.sub.unsubscribe();
  }
}



