import {Component, AfterViewInit, ViewEncapsulation, OnInit, DoCheck, } from '@angular/core';
import {CalcEmail} from "../../../../models/calc_email.model";
import {JSONBuilder} from "../../../../services/JSONBuilder.service";
import {FormulaService} from "../../../../services/formula.service";
import {BuilderService} from "../../../../services/builder.service";
import {FormBuilder, Validators, FormGroup} from "@angular/forms";
import {CustomValidator} from "../../../../../templates/services/customValidation";
declare var jQuery: any;

@Component({
    selector: 'config-email',
    // template:'hello this is email',
    template: require('./assets/html/email.template.html'),
    styles: [require('./assets/css/wysiwyg.css')],
    encapsulation: ViewEncapsulation.None,
})

export class ConfigEmailComponent implements AfterViewInit, OnInit, DoCheck {
  options: any;
  recommendCalcEmail: CalcEmail;
  finishCalcEmail: CalcEmail;
  emailForm: FormGroup;
  currentCount: number;
  constructor(private jsonBuilderHelper: JSONBuilder,
              private formulaService: FormulaService,
              private _builderService: BuilderService, fb: FormBuilder) {
    this.finishCalcEmail = new CalcEmail({ app: this.jsonBuilderHelper.getJSONBuilt()._id, type: 'Finish' });
    let validators: any[] = [];
    validators.push(Validators.required);
    validators.push(CustomValidator.emailFormat);
    this.emailForm = fb.group({
      email: [this.finishCalcEmail.email, Validators.compose([Validators.required])],
    });
  }

  ngDoCheck() {
    let self = this;
    if (this.currentCount === 0 || this.currentCount != this.formulaService.allValidVariables().length) {
      if (jQuery('textarea#froala-editorFinish').data('froala.editor'))
        jQuery('textarea#froala-editorFinish').froalaEditor('destroy');

      // if (jQuery('textarea#froala-editorRecommend').data('froala.editor'))
      //     jQuery('textarea#froala-editorRecommend').froalaEditor('destroy');

      this.currentCount = this.formulaService.allValidVariables().length;
      this.updateOptions();
      jQuery('textarea#froala-editorFinish').on('froalaEditor.buttons.refresh', function (e: any, editor: any) {
        self.updateOptions();
      });

      jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
      jQuery.FroalaEditor.RegisterCommand('questions', {
        title: 'Questions list',
        type: 'dropdown',
        focus: true,
        undo: true,
        icon: 'questions',
        refreshAfterCallback: true,
        options: this.options,
        callback: function (cmd: any, val: any) {
          this.html.insert(val);
        },
        refresh: function ($btn: any) { },
        refreshOnShow: function ($btn: any, $dropdown: any) { }
      });

      jQuery('textarea#froala-editorFinish').froalaEditor({
        toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'color', '|', 'fontSize', '|', 'questions'],
        pastePlain: true,
      });

      jQuery('textarea#froala-editorFinish').on('froalaEditor.contentChanged', function (e: any, editor: any) {
        self.finishCalcEmail.message = e.currentTarget.value;
        self.onFieldBlur(self.finishCalcEmail);

      });

      // jQuery('textarea#froala-editorRecommend').froalaEditor({
      //     toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'color', '|', 'fontSize', '|', 'questions'],
      //     pastePlain: true,
      // });

      // jQuery('textarea#froala-editorRecommend').on('froalaEditor.contentChanged', function (e: any, editor: any) {
      //     self.recommendCalcEmail.message = e.currentTarget.value;
      //     self.onFieldBlur(self.recommendCalcEmail);
      // });

    }
  }
  ngOnInit() {
    this.updateOptions();
    this.jsonBuilderHelper.updateTemplateQuestionare();

    this.recommendCalcEmail = new CalcEmail({ app: this.jsonBuilderHelper.getJSONBuilt()._id, type: 'Recommend' });
    this._builderService.calcEmail({ app: this.jsonBuilderHelper.getJSONBuilt()._id })
      .subscribe(

        (response: any[]) => {
          response.forEach((res) => {
            (res.type == 'Finish') ? this.finishCalcEmail = new CalcEmail(res)
              : this.recommendCalcEmail = new CalcEmail(res);
            jQuery('textarea#froala-editorFinish').froalaEditor('html.set', (this.finishCalcEmail.message));
            jQuery('textarea#froala-editorRecommend').froalaEditor('html.set', (this.recommendCalcEmail.message));
          });
        },
        (error: any) => { console.log(error); }
      );
  }

  ngAfterViewInit() {
    /* selectize js */
    let self = this;
    jQuery('.email-selectize').selectize();
    jQuery('.email-replyto-selectize').selectize({
      plugins: ['remove_button'],
      delimiter: ',',
      persist: false,
      create: function (input: any) {
        return {
          value: input,
          text: input
        };
      }
    });
    this.currentCount = 0;
  }

  onFieldBlur(value: CalcEmail) {
    if (!this.emailForm.valid)
      this.finishCalcEmail.sendEmail = false;
    let self = this;
    this._builderService.saveCalcEmail(value)
      .subscribe(
        (response: any) => {
          self.jsonBuilderHelper.getJSONBuilt().changed = true;
          value._id = response._id;
          self.jsonBuilderHelper.debounce(self.jsonBuilderHelper.animLoad(), 1800);
        },
        (error: any) => { console.log(error); }
      );
  }
  updateOptions() {
    this.options = '{ ';
    for (var variable in this.formulaService.allValidVariables())
      this.options += '" ' + this.formulaService.allValidVariables()[variable] + ' ":" ' + this.formulaService.allValidVariablesWysiywigList()[variable] + ' ",';

    this.options = this.options.slice(0, -1);
    this.options += '}';

    this.options = JSON.parse(this.options);
  }

}



