import { Component, AfterViewInit, ViewEncapsulation, DoCheck, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SubDomainService } from '../../../../../../shared/services/index';
import { JSONBuilder } from '../../../../services/JSONBuilder.service';
import { SafeUrl } from '../../../../../templates/pipes/safeUrl.pipe';
import { environment } from '../../../../../../../environments/environment';

declare var jQuery: any;

@Component({
  selector: 'config-embedded-code',
  template: require('./assets/html/embedded_code.template.html'),
  encapsulation: ViewEncapsulation.None
})

export class ConfigEmbeddedCodeComponent implements OnInit {
  hideSmallCode: boolean = true;
  hideFullCode: boolean = true;
  smallPageCode: any = '';
  fullPageCode: any = '';
  iloaderJS: any = '';
  src: any;
  constructor(private jsonBuilderHelper: JSONBuilder, private subDomainService: SubDomainService) { }

  ngOnInit() {
    if (this.jsonBuilderHelper.getJSONBuilt().template == 'sound-cloud')
      this.iloaderJS = '//' + environment.APP_EXTENSION + '/js/iloader.js';
    else
      this.iloaderJS = '//' + environment.APP_EXTENSION + '/js/loader/iloader.js';
    this.src = '//' + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt()._id;
    this.smallPageCode = `<div><div id='og-iframe-container' data-url='` + this.src + `' data-width='50%'></div><script src='` + this.iloaderJS + `' async></script></div>`;
    this.fullPageCode = `<div><div id='og-iframe-container' data-url='` + this.src + `' data-width='100%'></div><script src='` + this.iloaderJS + `' async></script></div>`;
  }
  scrollTo(param: any) {
    if (param == 'embed') {
      if (jQuery("#bh .copypaste-box").hasClass('hide')) {
        jQuery('html, body').animate({
          scrollTop: 350
        }, 200);
      }
    }
    else if (param == 'full') {
      if (jQuery("#bh2 .copypaste-box").hasClass('hide')) {
        jQuery('html, body').animate({
          scrollTop: 850
        }, 200);
      }
    }
  }
}



