import { Injectable }   from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Page } from '../../+builder/models/page.model';
import { Item } from '../../+builder/models/item.model';
import { Section } from '../../+builder/models/section.model';
import {CustomValidator} from './customValidation';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';

@Injectable()
export class TemplateValidatorService {

    forms: any;
    constructor(private jsonBuilderHelper: JSONBuilder) { }

    getFormGroups(): FormGroup[] {
        if(!this.forms) {
            this.jsonBuilderHelper.setValidatorService(this);
            let pages: Page[] = this.jsonBuilderHelper.getJSONBuilt().pages;
            let forms: any = {};

            pages.forEach(page => {
                if(page.type==='Questionnaire') {
                    page.sections.forEach(section => {
                        forms[section._id] = this.toFormGroup(section.items);
                    });
                }
            });
            this.forms = forms;
        }
        return this.forms;
    }

    toFormGroup(questions: Item[]): FormGroup {
        let group: any = {};

        questions.forEach(question => {
            let validators: any[] = [];
            if(question.type === 'checkbox' || question.type === 'radio_button' || question.type === 'switchbox') {
                group[question._id] = [];
                 question.options.forEach((option: any, index: any) => {
                    group[question._id][index] = new FormControl('');
                 });


                 if(question.config.validations.required.status === true)
                    group[question._id] = new FormGroup(group[question._id], CustomValidator.checkboxRequired(question));
                else
                    group[question._id] = new FormGroup(group[question._id]);
            }else {
                validators = this.composeValidators(question.config.validations, question);
                group[question._id] = new FormControl('', Validators.compose(validators));
            }

        });
        return new FormGroup(group);
    }

    composeValidators(validator: any, question:any): any[] {
        let validators: any[] = [];
        if(question.type === 'textfield' && question.config.type === 'number') {
                validators.push(CustomValidator.minimum(question.props.minVal));
                validators.push(CustomValidator.maximum(question.props.maxVal));
         }
        if(validator.required.status === true && question.type === 'textfield' ) {
            validators.push(Validators.required);
            // validation for numeric field
             if (question.config.type === 'email') {
                validators.push(CustomValidator.emailFormat);
            }else if(question.config.type === 'text'){
                validators.push(Validators.minLength(question.props.minVal));
            }
        }else {
            if(validator.required.status === true)
                validators.push(Validators.required);
        // if(validator.minLength.status === true)
        //     validators.push(Validators.minLength(question.props.minVal));
        }

        return validators;
    }

    updateFormGroup(section: Section) {
        let self: any = this;
        self.forms[section._id] = [];
        self.forms[section._id] = this.toFormGroup(section.items);
    }
}



