import { Component, ViewEncapsulation, OnInit, DoCheck, AfterViewInit } from '@angular/core';
import { JSONBuilder } from '../../../../services/JSONBuilder.service';
import { SafeUrl } from '../../../../../templates/pipes/safeUrl.pipe';
import { SubDomainService } from '../../../../../../shared/services/index';
import { environment } from '../../../../../../../environments/environment';

declare var jQuery: any;
@Component({
  selector: 'config-launch-popup',
  template: require('./assets/html/launch_popup.template.html'),
  encapsulation: ViewEncapsulation.None,
  styles: [require('./assets/css/fancybox.css')]
})

export class ConfigLaunchPopupComponent implements AfterViewInit, OnInit, DoCheck {
  iframeData: any;
  loaderJs: any;
  isButton: boolean = false;
  isDrawer: boolean = false;
  appExt: any = this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION;
  srcUrl: any = '//' + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt()._id;
  iFrameUrl: any = '';
  buttonName: any = '';
  bgColor: any = '';
  textColor: any = '';
  constructor(private jsonBuilderHelper: JSONBuilder, private subDomainService: SubDomainService) { }

  ngDoCheck() {
    if (document.title != 'Outgrow Home')
      document.title = "Outgrow Home";
  }

  updateEmbedCode() {
    this.bgColor = this.jsonBuilderHelper.getJSONBuilt().embedBgColor;
    this.textColor = this.jsonBuilderHelper.getJSONBuilt().embedTextColor;
    this.buttonName = this.jsonBuilderHelper.getJSONBuilt().embedTitle;
    this.srcUrl = '//' + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt()._id;
    if (this.isDrawer) {
      if (this.isButton)
        this.iframeData = `<div><div id='embed-container' data-textcolor='` + this.textColor + `' data-bgcolor='` + this.bgColor + `' data-prop='outgrow-d' data-type='outgrow-b' data-url='` + this.srcUrl + `' data-text='` + this.buttonName + `'></div><script src='` + this.loaderJs + `' async></script></div>`;
      else {
        this.textColor = this.jsonBuilderHelper.getJSONBuilt().embedLinkColor;
        this.iframeData = `<div><div id='embed-container' data-textcolor='` + this.textColor + `' data-bgcolor='` + this.bgColor + `' data-prop='outgrow-d' data-type='outgrow-l' data-url='` + this.srcUrl + `' data-text='` + this.buttonName + `'></div><script src='` + this.loaderJs + `' async></script></div>`;
      }
    } else {
      if (this.isButton)
        this.iframeData = `<div><div id='embed-container' data-textcolor='` + this.textColor + `' data-bgcolor='` + this.bgColor + `' data-prop='outgrow-p' data-type='outgrow-b' data-url='` + this.srcUrl + `' data-text='` + this.buttonName + `'></div><script src='` + this.loaderJs + `' async></script></div>`;
      else {
        this.textColor = this.jsonBuilderHelper.getJSONBuilt().embedLinkColor;
        this.iframeData = `<div><div id='embed-container' data-textcolor='` + this.textColor + `' data-bgcolor='` + this.bgColor + `' data-prop='outgrow-p' data-type='outgrow-l' data-url='` + this.srcUrl + `' data-text='` + this.buttonName + `'></div><script src='` + this.loaderJs + `' async></script></div>`;
      }
    }
  }
  ngAfterViewInit() {
    let self: any = this;
    //Button Color
    jQuery('#embed-btn-color').ColorPickerSliders({
      sliders: false,
      swatches: false,
      hsvpanel: true,
      previewformat: 'hex',
      size: 'large',
      placement: 'top',
      color: self.jsonBuilderHelper.getJSONBuilt().embedBgColor,
      onchange: function (container, color) {
        self.jsonBuilderHelper.getJSONBuilt().embedBgColor = '#' + color.tiny.toHex();
        self.updateEmbedCode();
      }
    });
    //Text/Link Color
    jQuery('#embed-text-color').ColorPickerSliders({
      sliders: false,
      swatches: false,
      hsvpanel: true,
      previewformat: 'hex',
      size: 'large',
      placement: 'top',
      color: self.jsonBuilderHelper.getJSONBuilt().embedBgColor,
      onchange: function (container, color) {
        self.jsonBuilderHelper.getJSONBuilt().embedTextColor = '#' + color.tiny.toHex();
        self.jsonBuilderHelper.getJSONBuilt().embedLinkColor = '#' + color.tiny.toHex();
        self.updateEmbedCode();
      }
    });
  }

  toggleDrawer() {
    localStorage.setItem('template', JSON.stringify(this.jsonBuilderHelper.getJSONBuilt()));
    this.iFrameUrl = environment.PROTOCOL + this.appExt + '/preview/previewFrame';
    jQuery('#drawerIframe')[0].contentWindow.location.reload(true);
    setTimeout(() => {
      jQuery("#drawerIframe").toggleClass("hide");
      jQuery("#close-drawer").toggleClass("hide");
      jQuery(".fancybox-drawer").toggleClass("hide");
      jQuery('html').css('overflow', 'hidden');

    }, 3000);
  }
  togglePopup() {
    this.iFrameUrl = environment.PROTOCOL + this.appExt + '/preview/previewFrame';
    localStorage.setItem('template', JSON.stringify(this.jsonBuilderHelper.getJSONBuilt()));
    console.log('home-button');
    //jQuery('html').css('overflow','hidden');

  }
  drawerClose() {
    jQuery("#drawerIframe").toggleClass("hide");
    jQuery("#close-drawer").toggleClass("hide");
    jQuery(".fancybox-drawer").toggleClass("hide");
    jQuery('html').css('overflow', 'initial');
  }
  ngOnInit() {
    this.loaderJs = '//' + environment.APP_EXTENSION + '/js/loader.js';
    this.srcUrl = '//' + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt()._id;
    jQuery('.fancybox').fancybox();
    jQuery(".fancybox-effects-d").fancybox({
      padding: 0, openEffect: 'elastic',
      openSpeed: 150, closeEffect: 'elastic',
      closeSpeed: 150, closeClick: true
    });
    this.updateEmbedCode();
  }
}



