import { Component, Input, OnInit, ViewEncapsulation, AfterViewInit, OnDestroy, OnChanges } from '@angular/core';
import { JSONBuilder } from '../../../services/JSONBuilder.service';
import { FormulaService } from '../../../services/formula.service';

declare var jQuery: any;
declare var math: any;

@Component({
  selector: 'editor-wysiwyg',
  template: require('./assets/html/editor_wysiwyg.component.html'),
  encapsulation: ViewEncapsulation.None,
})

export class EditorWysiwyg implements AfterViewInit, OnInit, OnDestroy, OnChanges {
  control: any;
  @Input() controls: any;
  editorHtml: string;
  currentCount: number;

  constructor(private jsonBuilderHelper: JSONBuilder, private formulaService: FormulaService) {
    if (jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical')
      this.control = jsonBuilderHelper.getSelectedControl();
  }
  ngOnInit() {
    if (this.controls != undefined)
      this.control = this.controls;
  }


  variableSelected(value: any) {
    //
  }

  ngOnChanges() {
    if (this.jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation') {  /* For Recommendation calc */
      if (this.control)
        jQuery('.wysiwyg' + this.control._id).froalaEditor('destroy');

      if(!this.jsonBuilderHelper.getSelectedFormula().hasOwnProperty('_id'))
        this.jsonBuilderHelper.getSelectedFormula()._id = 'formula'+Math.floor((Math.random() * 1000) * this.jsonBuilderHelper.getJSONBuilt().formula.length).toString();
      this.control = this.jsonBuilderHelper.getSelectedFormula();
      this.initWyswyg();
    }
  }

  ngAfterViewInit() {
    this.initWyswyg();
  }

  ngOnDestroy() {
    jQuery('.wysiwyg' + this.control._id).froalaEditor('destroy');
  }

  initWyswyg() {
    let self = this;
    /* wysiwyg editor */
    this.currentCount = 0;

    setTimeout(() => {
      var options: any = {};
      for (var variable in this.formulaService.allValidVariables())
        options[this.formulaService.allValidVariables()[variable]] = this.formulaService.allValidVariablesWysiywigList()[variable];

      jQuery('.wysiwyg' + this.control._id).on('froalaEditor.buttons.refresh', (e: any, editor: any) => {
        if (self.jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical')  /* For NUmerical calc */
          self.control.props.title = e.currentTarget.value;
        else if (self.jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation')  /* For Recommendation calc */
          self.control.html = e.currentTarget.value;

      });

      jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
      jQuery.FroalaEditor.RegisterCommand('questions', {
        title: 'Questions list',
        type: 'dropdown',
        focus: true,
        undo: true,
        icon: 'questions',
        refreshAfterCallback: true,
        options: options,
        callback: function (cmd: any, val: any) {
          this.html.insert(val);
        }
      });

      let editor: any;
      if (self.jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical') {  /* For NUmerical calc */
        editor = jQuery('.wysiwyg' + this.control._id).froalaEditor({
          heightMax: 250,
          toolbarButtons: ['bold', '|', 'italic', '|', 'underline', 'questions'],
        });
      } else if (self.jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation') {   /* For Recommendation calc */
        editor = jQuery('.wysiwyg' + this.control._id).froalaEditor({
          heightMax: 250,
          toolbarButtons: ['bold', '|', 'italic', '|', 'underline'],
        });
      }
      jQuery('.wysiwyg' + this.control._id).on('froalaEditor.contentChanged', (e: any, editor: any) => {
        if (self.jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical') {  /* For NUmerical calc */
          self.editorHtml = e.currentTarget.value;
          self.control.props.title = e.currentTarget.value;
        }
        else if (self.jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation') {  /* For Recommendation calc */
          self.editorHtml = e.currentTarget.value;
          self.control.html = e.currentTarget.value;
        }
      });
    }, 10);
  }
}



