import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'click-button',
  template: `
			 <div class="text-center" *ngIf="data.visible">
				<div>
					<button class="btn prime-action  next-step sliding-next "
						(click)="controlOutput.emit($event)"
					>
						{{data.props.title}}
					</button>
				</div>
			</div>
			 `,
  encapsulation: ViewEncapsulation.None
})

export class Button implements OnInit {
  @Input() data: any;
  @Output() controlOutput = new EventEmitter();
  value: string;

  ngOnInit() {}

}



