import {Component, AfterViewInit, OnInit, Output, EventEmitter} from '@angular/core';
import { FeatureAuthService } from '../../../../shared/services/feature-access.service';
declare var jQuery: any;

@Component({
  selector: 'component-manager-config',
  template: require('./component_config_manager.template.html'),
  styles: [require('./../assets/css/leftList.style.css')],

})

export class ComponentConfigManagerComponent implements AfterViewInit, OnInit {
  templateJson: any;
  selected: any = 'settings';
  isIntegerationAvailable: boolean = false;
  isEmbeddCodeAvailable: boolean = false;
  ConfigArray: any[] = ["settings", "integrations", "email", "share-your-calculator", "launch-popup", "embedded-code"];

  @Output() selection = new EventEmitter();
  constructor(private _featureAuthService: FeatureAuthService) { }

  ngOnInit() {
    this.isIntegerationAvailable = <boolean>this._featureAuthService.features.integrations;
    this.isEmbeddCodeAvailable = <boolean>this._featureAuthService.features.embedding;


    /*if (jQuery.inArray(localStorage.getItem('hash-link'), this.ConfigArray) != -1) {
      this.selected = localStorage.getItem('hash-link');
      jQuery('#config-' + this.selected).click();
    }*/
    //this.selection.emit(this.selected);
  }

  ngAfterViewInit() { }

  integrationClick(event: any) {
    this.isIntegerationAvailable = <boolean>this._featureAuthService.features.integrations;
    if (this.isIntegerationAvailable) {
      this.selected = 'integrations';
    }
    else {
      jQuery('#premiumModal').modal('show');
      this.selected = 'settings';
    }
  }

  embeddingClick(event: any) {
    this.isEmbeddCodeAvailable = <boolean>this._featureAuthService.features.embedding;
    if (this.isEmbeddCodeAvailable) {
      this.selected = 'embedded-code';
    }
    else {
      jQuery('#premiumModal').modal('show');
      this.selected = 'settings';
    }
  }

  onSelect(comp: string) {
    //window.location.hash = '#'+comp;
    this.selected = comp;
    this.selection.emit(comp);
  }

}




