import { Component} from '@angular/core';
import { JSONBuilder } from '../services/JSONBuilder.service';
import { JSONElement } from '../services/JSONElement.service';
import { ITEMS,RECOMMENDED_ITEMS } from '../models/itemNames.store';
@Component({
  selector: 'switch',
  template: `
				<select 
					class="select-default" 
					(change)="onChange($event)" 
					data-width="fit"
					class="form-control"
				>
			        <option *ngFor="let control of controls" value="{{control.value}}" 
						[selected]="jsonBuilderHelper.getSelectedControl().type==control.value">{{control.name}}
					</option>
			    </select>
	`,
})
export class Switch {

  controls: any[]=[];
  constructor(private jsonElementHandler: JSONElement ,private jsonBuilderHelper : JSONBuilder ) {
    let  switchItems = (jsonBuilderHelper.getJSONBuilt().templateType == 'Recommendation')?RECOMMENDED_ITEMS:ITEMS;
    for (var property in switchItems) {
      if (switchItems.hasOwnProperty(property)) {
        this.controls.push({
          value:property,
          name:switchItems[property]
        });
      }
    }
  }

  onChange($event:any) {
    let control = $event.target.value;
    this.jsonBuilderHelper.changeControl(control);
  }
}



