import {Component, AfterViewInit, Input, ViewEncapsulation } from '@angular/core';
import {OverviewComponent, UserDetailsComponent, TrafficDetailsComponent} from '../../../components/+analytics/components/index';
import { Script } from '../../../../shared/services/index';
import { App} from '../../models/model';
declare var jQuery: any;
declare var google: any;

@Component({
  selector: 'calc-analytics',
  template: require('./calc_analytics.component.html'),
  encapsulation: ViewEncapsulation.None,
  styles: [
    require('./../../../components/+analytics/assets/css/daterangepicker.css'),
    require('./../../../components/+analytics/assets/css/analytics.component.css')
  ],
})

export class CalcAnalyticsComponent implements AfterViewInit {
  @Input() calc: App;
  @Input() component: string;
  scriptsLoaded: boolean= false;

  constructor(private _script: Script) {//
  }

  ngAfterViewInit() {
    this._script.load('morrisCharts','datatables','daterangepicker')
      .then((data)=>{
        console.log('Scripts Loaded', data);
        this.scriptsLoaded = true;
      })
      .catch((error)=>{
        //any error
      });
  }
}




